.top_nav_override {
    background-color: #003366;
    color: #ffffff;
}
.top_nav_override .navbar {
    height: 32px;
    padding-top: 0px;
    padding-right: 0px;
}
.top_nav_override li {
    color: #ffffff;
    font-size: 14px;
}
.container_color {
    background-color: #e6e6e6;
}

.top_nav_style {
    height: 30px;
}
.header h1 {
    color: #003366;
    margin: 0;
    font-size: 2.8em;
    font-weight: bolder;
}
.header h2 {
    color: #003366;
    margin: 0;
    font-size: 1.7em;
    font-weight: bold;
}
.container-margins {
    margin-top: 1%;
    margin-bottom: 1%;
}
.container {
    padding: 0px;
}
#logo {
    width: 7em;
    height: 7em;
}
#mobile_only_text {
    display: none;
}

@media (min-width: 1150px) {
    .header h1 {
        font-size: 2em;
    }
    .header h2 {
        font-size: 1.2em;
    }
    #logo {
        margin-right: 0;
    }
}

@media (max-width: 1149px) and (min-width: 700px) {
    .header h1 {
        font-size: 2em;
    }
    .header h2 {
        font-size: 1.2em;
    }
    #logo {
        width: 6em;
        height: 6em;
    }
    .container-fluid nav > .navbar-nav {
        display: none;
    }
}

@media (max-width: 699px) and (min-width: 520px) {
    .header h1 {
        font-size: 1.8em;
        text-align: center;
    }
    .header h2 {
        font-size: 1em;
        text-align: center;
    }
    .container-fluid {
        display: none;
    }
    #logo {
        width: 5em;
        height: 5em;
    }
    #mobile_only_text {
        display: block;
    }
}
@media (max-width: 519px) {
    .header h1 {
        font-size: 1.5em;
        text-align: center;
    }
    .header h2 {
        font-size: 0.9em;
        text-align: center;
    }
    .container-fluid {
        display: none;
    }
    #logo {
        width: 4em;
        height: 4em;
    }
    #mobile_only_text {
        display: block;
    }
}
