body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top_nav_override {
    background-color: #003366;
    color: #ffffff;
}
.top_nav_override .navbar {
    height: 32px;
    padding-top: 0px;
    padding-right: 0px;
}
.top_nav_override li {
    color: #ffffff;
    font-size: 14px;
}
.container_color {
    background-color: #e6e6e6;
}

.top_nav_style {
    height: 30px;
}
.header h1 {
    color: #003366;
    margin: 0;
    font-size: 2.8em;
    font-weight: bolder;
}
.header h2 {
    color: #003366;
    margin: 0;
    font-size: 1.7em;
    font-weight: bold;
}
.container-margins {
    margin-top: 1%;
    margin-bottom: 1%;
}
.container {
    padding: 0px;
}
#logo {
    width: 7em;
    height: 7em;
}
#mobile_only_text {
    display: none;
}

@media (min-width: 1150px) {
    .header h1 {
        font-size: 2em;
    }
    .header h2 {
        font-size: 1.2em;
    }
    #logo {
        margin-right: 0;
    }
}

@media (max-width: 1149px) and (min-width: 700px) {
    .header h1 {
        font-size: 2em;
    }
    .header h2 {
        font-size: 1.2em;
    }
    #logo {
        width: 6em;
        height: 6em;
    }
    .container-fluid nav > .navbar-nav {
        display: none;
    }
}

@media (max-width: 699px) and (min-width: 520px) {
    .header h1 {
        font-size: 1.8em;
        text-align: center;
    }
    .header h2 {
        font-size: 1em;
        text-align: center;
    }
    .container-fluid {
        display: none;
    }
    #logo {
        width: 5em;
        height: 5em;
    }
    #mobile_only_text {
        display: block;
    }
}
@media (max-width: 519px) {
    .header h1 {
        font-size: 1.5em;
        text-align: center;
    }
    .header h2 {
        font-size: 0.9em;
        text-align: center;
    }
    .container-fluid {
        display: none;
    }
    #logo {
        width: 4em;
        height: 4em;
    }
    #mobile_only_text {
        display: block;
    }
}

.container {
    padding: 0px;
}

.main_header_nav_overrides li .home_alignment {
    margin-right: 10px;
}

.main_header_nav_overrides {
    background-color: #003366;
    border-bottom: 1px solid #cdcdcd;
    box-shadow: 0px 1px 2px #999;
    color: white;
}

.main_header_nav_overrides a.nav-link {
    color: white;
    text-align: center;
    text-decoration: none;
}

.main_header_nav_overrides a.nav-link:hover {
    color: black;
    border-radius: 5px;
    background-color: white;
}

.navbar-toggler {
    border-color: white;
    color: white;
}
.top_container_overrides {
    box-sizing: border-box;
    border: 1px solid #cdcdcd;
    box-shadow: 0px 1px 2px #999;
    padding: 0px 15px 15px 15px;
}

.img-class {
    padding-bottom: 15px;
}
.img-class > img {
    border-radius: 5px;
    border: 1px solid #cdcdcd;
    vertical-align: bottom;
}
.paragraph_style {
    border-bottom: 1px solid rgba(202, 202, 202, 0.733); 
    display:flex;
}
.paragraph_style h4 {
    color: #555; 
    font-weight: 100; 
    border-bottom: 2px solid #004581; 
    margin-bottom: -0.50px;
}

p {
    font-size: 13px; 
    padding-top: 20px; 
    color: #555;
}
h4, p {
    font-family: 'Roboto', sans-serif;
}
#top_container_overrides {
    box-sizing: border-box;
    border: 1px solid #cdcdcd;
    box-shadow: 0px 1px 2px #999;
}

h4 {
    font-family: 'Roboto', sans-serif;
    color: #555; 
    font-weight: 100;
    margin-top: 2%;
}

.card-img-top {
    width: 200px;
    height: 200px;
}
.card-deck {
    padding-bottom: 15px;
}
.card-body {
    padding: 0px;
}
.contact_section h2 {
    font-size: 1.2em;
    font-family: 'Roboto', sans-serif;
    color: #444444;
}
.contact_section {
    background: #fffeff;
    padding: 15px;
    height: 200px;
    border: 1px solid #fff7fd;
    margin: 0px;
    box-shadow: 1px 1px 2px #e2e0e0;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

