.container {
    padding: 0px;
}

.main_header_nav_overrides li .home_alignment {
    margin-right: 10px;
}

.main_header_nav_overrides {
    background-color: #003366;
    border-bottom: 1px solid #cdcdcd;
    box-shadow: 0px 1px 2px #999;
    color: white;
}

.main_header_nav_overrides a.nav-link {
    color: white;
    text-align: center;
    text-decoration: none;
}

.main_header_nav_overrides a.nav-link:hover {
    color: black;
    border-radius: 5px;
    background-color: white;
}

.navbar-toggler {
    border-color: white;
    color: white;
}