#top_container_overrides {
    box-sizing: border-box;
    border: 1px solid #cdcdcd;
    box-shadow: 0px 1px 2px #999;
}

h4 {
    font-family: 'Roboto', sans-serif;
    color: #555; 
    font-weight: 100;
    margin-top: 2%;
}

.card-img-top {
    width: 200px;
    height: 200px;
}
.card-deck {
    padding-bottom: 15px;
}
.card-body {
    padding: 0px;
}
.contact_section h2 {
    font-size: 1.2em;
    font-family: 'Roboto', sans-serif;
    color: #444444;
}
.contact_section {
    background: #fffeff;
    padding: 15px;
    height: 200px;
    border: 1px solid #fff7fd;
    margin: 0px;
    box-shadow: 1px 1px 2px #e2e0e0;
}