.top_container_overrides {
    box-sizing: border-box;
    border: 1px solid #cdcdcd;
    box-shadow: 0px 1px 2px #999;
    padding: 0px 15px 15px 15px;
}

.img-class {
    padding-bottom: 15px;
}
.img-class > img {
    border-radius: 5px;
    border: 1px solid #cdcdcd;
    vertical-align: bottom;
}
.paragraph_style {
    border-bottom: 1px solid rgba(202, 202, 202, 0.733); 
    display:flex;
}
.paragraph_style h4 {
    color: #555; 
    font-weight: 100; 
    border-bottom: 2px solid #004581; 
    margin-bottom: -0.50px;
}

p {
    font-size: 13px; 
    padding-top: 20px; 
    color: #555;
}
h4, p {
    font-family: 'Roboto', sans-serif;
}